"ã‚ã‚‹æ—¥ã€ãƒˆãƒ¼ãƒžã‚¹ã¯ã€Œèµ·ãã‚ã€ãƒã‚ªã€ã€Œãƒžãƒˆãƒªãƒƒã‚¯ã‚¹ãŒè¦‹ã¦ã„ã‚‹ã€ã€Œç™½ã‚¦ã‚µã‚®ã«ã¤ã„ã¦è¡Œã‘ã€ã¨ã„ã†è¬Žã®ãƒ¡ãƒ¼ãƒ«ã‚’å—ã‘å–ã‚‹ã€‚ã»ã©ãªãã—ã¦ãƒˆãƒªãƒ‹ãƒ†ã‚£ã¨åä¹—ã‚‹è¬Žã®å¥³æ€§ã¨å‡ºä¼šã£ãŸãƒˆãƒ¼ãƒžã‚¹ã¯ã€ãƒˆãƒªãƒ‹ãƒ†ã‚£ã®ä»²é–“ã®ãƒ¢ãƒ¼ãƒ•ã‚£ã‚¢ã‚¹ã‚’ç´¹ä»‹ã•ã‚Œã€Œè²´æ–¹ãŒç”Ÿãã¦ã„ã‚‹ã“ã®ä¸–ç•Œã¯ã€ã‚³ãƒ³ãƒ”ãƒ¥ãƒ¼ã‚¿ã«ã‚ˆã£ã¦ä½œã‚‰ã‚ŒãŸä»®æƒ³ç¾å®Ÿã ã€ã¨å‘Šã’ã‚‰ã‚Œã€ã“ã®ã¾ã¾ä»®æƒ³ç¾å®Ÿã§ç”Ÿãã‚‹ã‹ã€ç¾å®Ÿã®ä¸–ç•Œã§ç›®è¦šã‚ã‚‹ã‹ã®é¸æŠžã‚’è¿«ã‚‰ã‚Œã‚‹ã€‚æ—¥å¸¸ã®é•å’Œæ„Ÿã«æ‚©ã¾ã•ã‚Œã¦ã„ãŸãƒˆãƒ¼ãƒžã‚¹ã¯ç¾å®Ÿã®ä¸–ç•Œã§ç›®è¦šã‚ã‚‹äº‹ã‚’é¸æŠžã™ã‚‹ã€‚æ¬¡ã®çž¬é–“ã€ãƒˆãƒ¼ãƒžã‚¹ã¯è‡ªåˆ†ãŒåŸ¹é¤Šæ§½ã®ã‚ˆã†ãªã‚«ãƒ—ã‚»ãƒ«ã®ä¸­ã«é–‰ã˜è¾¼ã‚ã‚‰ã‚Œã€èº«å‹•ãã‚‚ã§ããªã„çŠ¶æ…‹ã§ã‚ã‚‹ã“ã¨ã«æ°—ä»˜ãã€‚ãƒˆãƒªãƒ‹ãƒ†ã‚£é”ã®è¨€ã£ãŸã“ã¨ã¯çœŸå®Ÿã§ã€ç¾å®Ÿã®ä¸–ç•Œã¯ã‚³ãƒ³ãƒ”ãƒ¥ãƒ¼ã‚¿ã®åä¹±[5]ã«ã‚ˆã£ã¦äººé–“ç¤¾ä¼šãŒå´©å£Šã—ã€äººé–“ã®å¤§éƒ¨åˆ†ã¯ã‚³ãƒ³ãƒ”ãƒ¥ãƒ¼ã‚¿ã®å‹•åŠ›æºã¨ã—ã¦åŸ¹é¤Šã•ã‚Œã¦ã„ãŸã€‚è¦šé†’ã—ã¦ã—ã¾ã£ãŸãƒˆãƒ¼ãƒžã‚¹ã¯ä¸è‰¯å“ã¨ã—ã¦å»ƒæ£„ã•ã‚Œã‚‹ãŒã€å¾…ã¡æ§‹ãˆã¦ã„ãŸãƒˆãƒªãƒ‹ãƒ†ã‚£ã¨ãƒ¢ãƒ¼ãƒ•ã‚£ã‚¢ã‚¹ã«æ•‘ã‚ã‚ŒãŸã€‚" +
"ãƒˆãƒ¼ãƒžã‚¹ã¯ã€ãƒ¢ãƒ¼ãƒ•ã‚£ã‚¢ã‚¹ãŒèˆ¹é•·ã‚’å‹™ã‚ã‚‹å·¥ä½œèˆ¹ã€Œãƒãƒ–ã‚«ãƒ‰ãƒã‚¶ãƒ«å·ã€ã®ä»²é–“ã¨ã—ã¦è¿Žãˆã‚‰ã‚Œã€ãƒãƒƒã‚«ãƒ¼ã¨ã—ã¦ä½¿ã£ã¦ã„ãŸåå‰ã€Œãƒã‚ªã€ã‚’åä¹—ã‚‹ã“ã¨ã«ãªã£ãŸã€‚ãƒ¢ãƒ¼ãƒ•ã‚£ã‚¢ã‚¹ã¯ãƒã‚ªã“ããŒã‚³ãƒ³ãƒ”ãƒ¥ãƒ¼ã‚¿ã®æ”¯é…ã‚’æ‰“ã¡ç ´ã‚‹æ•‘ä¸–ä¸»ã§ã‚ã‚‹ã¨ä¿¡ã˜ã¦ãŠã‚Šã€ä»®æƒ³ç©ºé–“ã§ã®èº«ä½“ã®ä½¿ã„æ–¹ã‚„ã€æ‹³æ³•ãªã©ã®æˆ¦é—˜æŠ€è¡“ã‚’ç¿’å¾—ã•ã›ãŸã€‚äººé¡žã®æŠµæŠ—è»ã®ä¸€å“¡ã¨ãªã£ãŸãƒã‚ªã¯ã€ä»®æƒ³ç©ºé–“ã¨ç¾å®Ÿã‚’è¡Œãæ¥ã—ãªãŒã‚‰ã€äººé¡žã‚’ã‚³ãƒ³ãƒ”ãƒ¥ãƒ¼ã‚¿ã®æ”¯é…ã‹ã‚‰è§£æ”¾ã™ã‚‹æˆ¦ã„ã«èº«ã‚’æŠ•ã˜ã‚‹äº‹ã«ãªã£ãŸã€‚" +
"ã€Šé»‘å®¢å¸å›½ã€‹ï¼ˆè‹±è¯­ï¼šThe Matrixï¼‰æ˜¯ä¸€éƒ¨1999å¹´çš„å¥½èŽ±åžç§‘å¹»ç”µå½±ï¼Œç”±æ²ƒå“æ–¯åŸºå§å¦¹æ‰§å¯¼ï¼ŒåŸºåŠªÂ·é‡Œç»´æ–¯ã€åŠ³ä¼¦æ–¯Â·è²ä»€ä¼¯æ©ã€å‡¯èŽ‰Â·å®‰æ‘©ä¸åŠé›¨æžœÂ·å¨æ–‡ç­‰äººä¸»æ¼”ï¼Œå¹¶ç”±é¦™æ¸¯ç”µå½±ç•Œçš„è¢å’Œå¹³æ‹…ä»»æ­¦æœ¯æŒ‡å¯¼ã€‚æ­¤ç‰‡ä»¥å…¶ç‹¬åˆ°çš„å“²å­¦å’Œå­å¼¹æ—¶é—´çš„ç‰¹æ®Šæ…¢é•œå¤´åŠå„å¼ç”µè„‘ç‰¹æ•ˆè‘—åï¼Œåœ¨å…¨çƒèŽ·å–äº®çœ¼ç¥¨æˆ¿ï¼Œå¹¶åœ¨2003å¹´ï¼ŒæŽ¨å‡ºç»­é›†ã€Šé»‘å®¢å¸å›½2ï¼šé‡è£…ä¸Šé˜µã€‹åŠç¬¬ä¸‰é›†ã€Šé»‘å®¢å¸å›½3ï¼šçŸ©é˜µé©å‘½ã€‹";

function ShuffleString(str, len) {
    let result = "";
    let str_len = str.length;
    for (let i = 0; i < len; i++) {
        let rd = Math.round(Math.random(new Date()) * str_len);
        result += str[rd];
    }
    return result;
}

const total_level = 128;
const black_level = 72;

export class VirtualLine {

    constructor(x_offset, width, height, font_width, font_height) {
        this._x_offset = x_offset;
        this._width = width;
        this._height = height;

        this._font_width = font_width;
        this._font_height = font_height;

        this._content = ShuffleString(resources, total_level);

        this._tick = total_level;
        this._offset_step = Math.round(Math.random() * total_level);
    }

    paint(ctx, deltaTime) {
        const text_length = Math.ceil(this._height / this._font_height);
        for (let i = 0; i < text_length; i++) {
            const absolute_level = (i + this._offset_step - this._tick) % total_level + total_level;
            if (absolute_level > black_level) {
                const color_level = absolute_level - black_level;
                const green_level = Math.round((color_level / (total_level - black_level)) * 255);
                if (green_level === 255) {
                    ctx.shadowColor = "white";
                    ctx.shadowOffsetX = 0;
                    ctx.shadowOffsetY = 0;
                    ctx.shadowBlur = 10;
                    ctx.fillStyle = "rgb(20," + green_level.toString() + ",20)"; 
                } else {
                    ctx.shadowBlur = 0;
                    ctx.fillStyle = "rgb(0," + green_level.toString() + ",0)"; 
                }
                ctx.fillText(this._content[i], this._x_offset, i * this._font_height);
            }
        }
        this._tick++;

        const test_level = (this._offset_step - this._tick) % total_level + total_level;
        if (test_level === 1) {
            this._content = ShuffleString(resources, total_level);
        }
        /*
        if (this._tick % this._offset_step === 0) {
            this._content = ShuffleString(resources);
        }
        */
    }

    get x_offset() {
        return this._x_offset;
    }

    get width() {
        return this._width;
    }

    set width(value) {
        this._width = value;
    }

    get height() {
        return this._height;
    }

    set height(value) {
        this._height = value;
    }

}



